import axios from "axios"

const api = axios.create({
    baseURL: "https://vtrace-backend.herokuapp.com/api",
    // baseURL: "http://localhost:5000/api",
    withCredentials: true,
    headers: {
        auth_token: localStorage.getItem("vtraceToken"),
    },
})

//Establisment Authentication

export async function getAdmin(username) {
    try {
        const response = await api.post("/admin/individual", {
            username: username,
        })
        return response.data
    } catch (error) {
        console.error(error)
        return { success: false, message: "Failed to create admin." }
    }
}

export async function createAdmin(data) {
    try {
        const response = await api.post("/admin/create", data)
        return response.data
    } catch (error) {
        console.error(error)
        return { success: false, message: "Failed to create admin." }
    }
}

export async function adminLogin(username, password) {
    try {
        const response = await api.post("/admin/login", {
            username: username,
            password: password,
        })
        if (response.data.success) {
        }
        return response.data
    } catch (error) {
        console.error(error)
        return { success: false, message: "Failed to login." }
    }
}

export async function checkAdminLoggedIn(username, password) {
    try {
        const response = await api.post("/admin/login", {
            username: username,
            password: password,
        })
        return response.data
    } catch (error) {
        console.error(error)
        return { success: false, message: "Failed to login." }
    }
}

export async function adminLogout() {
    try {
        await api.get("/admin/logout")
    } catch (error) {
        console.error(error)
    }
}
